$addWidth: 340px;
$addHeight: calc($addWidth / 1.618) + 30;

.mainSection{
    display: grid;
    gap: 15px;
    grid-template-columns:1fr 1fr 1fr;
    

    justify-content: stretch;
    // align-items: stretch;
    // justify-content: center;
    // background-color: pink;
    height: 100%;
    padding: 0px 15%;
    padding-top: 20px;

    .mainSection_add{
        grid-row: 1/3;
        grid-auto-rows: minmax(100px, auto);

        display: flex;
        justify-content: center;
        align-items: center;
        // width: $addWidth;
        min-height: $addHeight;
        border-radius: 15px;
        // margin: 10px;
        background-color: #f2f2f2;
        clip-path: inset(0% 0% round 10px);
        background-image: url('../../../public/inbox.png');

        .mainSection_add_wrapper{
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;
            height: 100%;
            background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0));
            backdrop-filter: blur(30px);
            -webkit-backdrop-filter: blur(10px);


            .mainSection_add_wrapper-btn{
                border: none;
                border-radius: 100%;
                width: 50px;
                height: 50px;
                background-color: white;
                font-size: 18px;
                font-weight: 600;
            }
        }
    }
}



@media only screen and (max-width: 1300px){
    .mainSection{
        grid-template-columns:1fr 1fr;
    }
}
@media only screen and (max-width: 1000px){
    .mainSection{
        grid-template-columns:1fr;
        padding: 0px 10%;
        padding-top: 20px;

        .mainSection_add{
            min-height: $addHeight - 50;
        }
    }
}
