.headerSection{
    display: flex;
    justify-content: space-between;
    padding: 0px 5%;
    // background-color: aquamarine;
    
    .headerSection_logo{

        .headerSection_logo_p{
            font-weight: 800;
            font-size: 18px;
        }
    }

    .headerSection_themeSwitcher{
        display: flex;
        align-items: center;
        // background-color: cadetblue;
        
        .headerSection_themeSwitcher-svg{
            width: 25px;
        }
    }

    .headerSection_menuOpener{
        height: 11px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-self: center;
        // background-color: yellowgreen;

        .headerSection_menuOpener-line{
            width: 20px;
            height: 3px;
            background-color: #3b3c3b;
        }
    }
}