$addWidth: 340px;
$addHeight: calc($addWidth / 1.618 / 2.5 );

.cardsSection{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    min-width: $addWidth;
    height: $addHeight;
    border-radius: 15px;
    padding: 4% 0px;
    background-color: #f2f2f2;
    // background-color: pink;

    .cardsSection-p{
        padding: 0px;
        margin: 0px;
        margin-left: 10%;
        // margin-top: 6px;
        font-weight: 700;
        color: #383232;
        
    }

    .cardsSection_ul{
        display: flex;
        // justify-content: space-between;
        list-style-type: none;
        padding: 0px;
        margin: 0px 5%;

        li{
            display: flex;
            align-items: center;
            margin-right: 3%;
            // width: 5rem;
            padding: 0px 15px;
            height: 35px;
            border-radius: 100px;
            background-color: #e5e5e5;

            white-space: nowrap;
            text-align: center;
            font-size: 65%;
            font-weight: 600;
            color: #99989c;

            
        }
    }
}

@media only screen and (max-width: 1000px){
    .cardsSection{
        min-width: $addWidth - 100px;
        height: $addHeight;

        .cardsSection-p{
       
        }
    
        .cardsSection_ul{
            padding: 0px;
            margin: 0px 5%;
    
            li{
                margin-right: 2%;
                padding: 0px 10px;
                height: 30px;

                font-size: 65%;

            }
        }
    }
}

@media only screen and (max-width: 350px){
    .cardsSection{

        .cardsSection_ul{
            // background-color: red;
            overflow-y: scroll;
           
        }
    }
}